//1引入组件 uni-load-more.vue
import uniLoadMore from '@/components/uni-load-more.vue';
import {
	friendlyDate
} from '@/common/util.js';
var _self,
	page = 1,
	timer = null;
// 定义全局参数,控制数据加载

export default {
	components: { //2注册组件
		uniLoadMore
	},
	data: {
		newsList: [],
		title: "",
		storage_key:'',
		loadingText: '加载中...',
		loadingType: 0, //定义加载方式 0---contentdown  1---contentrefresh 2---contentnomore
		contentText: {
			contentdown: '上拉显示更多',
			contentrefresh: '正在加载...',
			contentnomore: '没有更多数据了'
		}
	},
     
	onLoad: function(e) {
		//console.log("接收id:" + JSON.stringify(e.iconId));
		this.storage_key =  e.iconId;
		_self = this;
		uni.setNavigationBarTitle({
			title: e.name
		});
		//页面一加载时请求一次数据
		this.getnewsList();
	},
	onPullDownRefresh: function(e) {
		//下拉刷新的时候请求一次数据
		this.getnewsList();
	},
	onReachBottom: function(e) {
		//console.log("再次接收id:" + JSON.stringify(e.iconId));
		//触底的时候请求数据，即为上拉加载更多
		//为了更加清楚的看到效果，添加了定时器
		if (timer != null) {
			clearTimeout(timer);
		}
		timer = setTimeout(function(e) {
			_self.getmorenews(e);
		}, 1000);
	},
	methods: {
		getmorenews(e) {
			if (_self.loadingType !== 0) { //loadingType!=0;直接返回
				return false;
			}
			_self.loadingType = 1;
			uni.showNavigationBarLoading(); //显示加载动画
			uni.request({
				url: 'https://unidemo.dcloud.net.cn/api/news?pageSize=' + page * 10+ '&columnId=' + this.storage_key,
				method: 'GET',
				success: function(res) {
					console.log(JSON.stringify(res));
					const data = res.data.map((news) => {
						return {
							id: news.id,
							author_avatar: news.author_avatar,
							article_type: 1,
							datetime: friendlyDate(new Date(news.published_at.replace(/\-/g, '/')).getTime()),
							title: news.title,
							image_url: news.cover,
							author_name: news.author_name,
							comments_count: news.comments_count,
							post_id: news.post_id
						};
					});
					if (res.data == null) { //没有数据
						_self.loadingType = 2;
						uni.hideNavigationBarLoading(); //关闭加载动画
						return;
					}
					page++; //每触底一次 page +1
					// _self.newsList = data;
					_self.newsList =_self.newsList.concat(data);
					_self.loadingType = 0; //将loadingType归0重置
					uni.hideNavigationBarLoading(); //关闭加载动画
				}
			});
		},
		getnewsList(e) { //第一次回去数据
			page = 1;
			this.loadingType = 0;
			uni.showNavigationBarLoading();
			uni.request({
				url: 'https://unidemo.dcloud.net.cn/api/news?pageSize=' + page * 10 + '&columnId=' + this.storage_key,
				method: 'GET',
				success: function(res) {
					page++; //得到数据之后page+1
					const data = res.data.map((news) => {
						return {
							id: news.id,
							author_avatar: news.author_avatar,
							article_type: 1,
							datetime: friendlyDate(new Date(news.published_at.replace(/\-/g, '/')).getTime()),
							title: news.title,
							image_url: news.cover,
							author_name: news.author_name,
							comments_count: news.comments_count,
							post_id: news.post_id
						};
					});
					_self.newsList = data;
					uni.hideNavigationBarLoading();
					uni.stopPullDownRefresh(); //得到数据后停止下拉刷新
				}
			});
		},
		openNew(e) {
			//console.log("详情信息:"+JSON.stringify(e));		
			var newsid = e.post_id;
			uni.navigateTo({
				url: '../info/info?newsid=' + e.post_id,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}
	}
};
